
.DragDropAxis {
    background-color: transparent;
    transition: background-color 0.2s, border-color 0.2s;
    cursor: move;
}


.DragDropAxis.drag-over,
.DragDropAxis:hover {
    background-color: #EEE;
}

.DragDropAxis.selected {
    background-color: #96d1fb;
}

.DragDropAxis.selected:hover {
    opacity: 0.5;
}

.DragDropAxis.drag:hover,
.DragDropAxis.drag.drag-over {
    background-color: initial;
}

