.drop-zone {
    background-color: #DDD;
    border: 1px dashed black;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: background-color 0.2s, border-color 0.2s;
}

.drop-zone:hover {
    background-color: #CCC;
    border-color: blue;
}

.drop-zone.drag-over {
    background-color: lightblue;
    border-color: blue;
}